#tvs-flow-editor .main-header {
  user-select: none;
  cursor: grab;
}
#tvs-flow-editor .main-content {
  user-select: none;
}
#tvs-flow-editor .resize-drag {
  position: absolute;
  display: block;
  right: 0;
  bottom: 0;
  width: 20px;
  height: 20px;
  cursor: grab;
  background: linear-gradient(to bottom right, rgba(0,0,0,0) 50%, rgba(0,0,0,0.2) 51%);
}
#tvs-flow-editor .entity-component,
#tvs-flow-editor .process-component {
  margin: 5px;
  padding: 10px;
  background-color: rgba(255,255,255,0.6);
  border-top: 1px solid #fff;
  box-shadow: 0 2px 5px rgba(0,0,0,0.3);
  border-radius: 4px;
}
#tvs-flow-editor .CodeMirror {
  height: auto;
  font-family: "Source Code Pro", Monaco, Menlo, "Ubuntu Mono", Consolas, source-code-pro, monospace;
  line-height: 1.5;
  font-size: 16px;
}
